// @flow
import "./index.css";
import registerServiceWorker from "./registerServiceWorker";
import * as THREE from "three";
import CreateOrbitControls from "three-orbit-controls"; // покрутить камеру
import perlinNoise from "./perlin-noise";

const OrbitControls = CreateOrbitControls(THREE);

class App {
  scene: THREE.Scene;
  renderer: THREE.WebGLRenderer;
  camera: THREE.PerspectiveCamera;
  loader: THREE.TextureLoader;
  planeGeometry: THREE.PlaneGeometry;
  planeMaterial: THREE.ShaderMaterial;

  controls: OrbitControls;

  rootElement: null | HTMLElement = document.getElementById("root");

  time: number = 0;

  constructor() {
    this.scene = new THREE.Scene();
    this.scene.background = new THREE.Color(0x000000);

    this.renderer = new THREE.WebGLRenderer();
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.setSize(window.innerWidth, window.innerHeight);

    // $FlowFixMe
    this.rootElement.appendChild(this.renderer.domElement);

    this.camera = new THREE.PerspectiveCamera(
      90,
      window.innerWidth / window.innerHeight,
      1,
      3000
    );
    this.camera.position.set(-5, -358, 80);

    this.controls = new OrbitControls(this.camera, this.renderer.domElement);

    this.createPlane();
  }

  // @deprecated
  createLine() {
    // число линий
    const lineCount = 20;
    // создаем материал, из которого будут создаваться объекты
    const material = new THREE.LineBasicMaterial({ color: 0xffffff });
    // создаем геометрию, которая говорит как эти объекты будут распологаться(набор точек)
    const geometry = new THREE.Geometry();

    for (let index = 0; index < lineCount; index++) {
      geometry.vertices.push(new THREE.Vector3(index * 10, 0, 0));
    }

    // смешиваем геометрию с материалом
    const line = new THREE.Line(geometry, material);

    this.scene.add(line);
  }

  createPlane() {
    // материал с шейдерами
    const material = (this.planeMaterial = new THREE.ShaderMaterial({
      // Рисовать геометрию в виде каркаса (используя GL_LINES вместо GL_TRIANGLES). Значение по умолчанию - false (т. Е. Рендер как плоские полигоны).
      // явно показывает разбиение на треугольники
      // wireframe: true,
      extensions: {
        // включаем производные в шейдерах
        derivatives: "#extension GL_OES_standard_derivatives : enable"
      },
      uniforms: {
        // передаем значения в шейдеры
        time: { type: "f", value: 0.0 }
      },
      // шейдер отвечающий за геометрию, за точки, за позицию точек
      // $FlowFixMe
      vertexShader: document.getElementById("vertexShader").textContent,
      // шейдер отвечающий за цвета, падающий свет и тд
      // $FlowFixMe
      fragmentShader: document.getElementById("fragmentShader").textContent,
      // отрисовываем с 2х сторон
      side: THREE.DoubleSide,
      // Определяет прозрачность этого материала
      transparent: true
    }));

    // размеры геометрии width, height
    const size = [600, 600];
    // размеры сегмента(треугольника заполняющего plane)
    const triangleSize = [200, 200];
    const geometry = (this.planeGeometry = new THREE.PlaneGeometry(
      ...size,
      ...triangleSize
    ));

    // абстрактное смешивание
    const mesh = new THREE.Mesh(geometry, material);

    this.scene.add(mesh);
  }

  // @deprecated
  updatePlane() {
    const { planeGeometry } = this;
    const { vertices } = planeGeometry;

    for (let index = 0; index < vertices.length; index++) {
      const vec = vertices[index];
      vec.z = 100 * perlinNoise(vec.x / 100, vec.y / 100, this.time / 100);
    }

    planeGeometry.verticesNeedUpdate = true;
  }

  updatePlaneMaterialTime() {
    this.planeMaterial.uniforms.time.value = this.time;
  }

  animate() {
    this.time++;
    // this.updatePlane();
    this.updatePlaneMaterialTime();
    requestAnimationFrame(this.animate.bind(this));
    this.render();
  }

  render() {
    this.renderer.render(this.scene, this.camera);
  }
}

registerServiceWorker();

const app = new App();
app.animate();



